import React, { useEffect, useState } from "react";
import { useGetLocale } from "providers";

import { Tab, Tabs, Pagination } from "components";

import { Template } from "../Components";
import { DepositTable } from "./DepositTable";
import { WithdrawTable } from "./WithdrawTable";
import css from "./style.module.scss";

const Records = () => {
  const intl = useGetLocale();

  const [tab, setTab] = useState(0);
  const [isLoading, setIsLoading] = useState(false);

  const [withdrawTotalPages, setWithdrawTotalPages] = useState(1);
  const [depositTotalPages, setDepositTotalPages] = useState(1);
  const [totalPages, setTotalPages] = useState(1);

  const [depositPage, setDepositPage] = useState(1);
  const [withdrawPage, setWithdrawPage] = useState(1);
  const [page, setPage] = useState(1);

  const handleTabChange = (e: any, value: any) => {
    setTab(value);
    setPage(1);
  };

  const handleChangePage = (p: number) => {
    if (!isLoading) {
      setPage(p);

      switch (tab) {
        case 0:
          setDepositPage(p);
          break;

        case 1:
          setWithdrawPage(p);
          break;
      }
    }
  };

  useEffect(() => {
    switch (tab) {
      case 0:
        setTotalPages(depositTotalPages);
        setPage(depositPage);
        break;

      case 1:
        setTotalPages(withdrawTotalPages);
        setPage(withdrawPage);
        break;
    }
  }, [tab, depositTotalPages, withdrawTotalPages]);

  return (
    <Template>
      <div className={css.root}>
        <div className={css.paginationBlock}>
          <Pagination
            page={page}
            pageAmount={totalPages}
            onChange={handleChangePage}
            showFirstLastPage
            classes={{
              root: css.pagination_root,
              button: css.pagination_button,
              activePage: css.pagination_active,
            }}
          />
        </div>

        <Tabs
          value={tab}
          onChange={handleTabChange}
          highlighterClass={css.tabIndicator}>
          <Tab
            className={css.tab}
            label={intl.formatMessage({ id: "wallet.records.deposit" })}
          />
          <Tab
            className={css.tab}
            label={intl.formatMessage({ id: "wallet.records.withdraw" })}
          />
        </Tabs>

        <div className={css.swipe}>
          <div
            className={`${css.swipeUnit} ${tab === 1 ? css.swipeDisable : ""}`}>
            <DepositTable
              page={depositPage}
              setTotalPages={setDepositTotalPages}
              setPage={setDepositPage}
              setIsLoading={setIsLoading}
            />
          </div>
          <div
            className={`${css.swipeUnit} ${tab === 0 ? css.swipeDisable : ""}`}>
            <WithdrawTable
              page={withdrawPage}
              setTotalPages={setWithdrawTotalPages}
              setPage={setWithdrawPage}
              setIsLoading={setIsLoading}
            />
          </div>
        </div>

        <div className={css.paginationBlock}>
          <Pagination
            page={page}
            pageAmount={totalPages}
            onChange={handleChangePage}
            showFirstLastPage
            classes={{
              root: css.pagination_root,
              button: css.pagination_button,
              activePage: css.pagination_active,
            }}
          />
        </div>
      </div>
    </Template>
  );
};

export default Records;
