import React, { FC } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import css from "./style.module.scss";
interface Props {
  transaction_id: string;
  method: string;
  classes: {
    [key: string]: string;
    bLabel: string;
    bInfo: string;
  };
  link: string;
}

const Help: FC<Props> = ({ transaction_id, method, classes, link }) => {
  const intl = useGetLocale();

  return (
    <div className={css.root}>
      <div className={css.box}>
        <div className={css.wrapper}>
          <h5 className={css.title}>
            {intl.formatMessage({ id: "wallet.records.transaction_id" })}:
          </h5>
          {transaction_id ? (
            <a
              href={link === "#" ? "" : link}
              rel="noreferrer"
              target="_blank"
              className={clsx(css.url, css.text)}>
              {transaction_id}
            </a>
          ) : (
            <span>-</span>
          )}
        </div>
        <div className={css.wrapper}>
          <h5 className={css.title}>
            {intl.formatMessage({ id: "wallet.records.method" })}:
          </h5>
          <label className={`${classes.bLabel} ${classes.bInfo}`}>
            {method}
          </label>
        </div>
      </div>
    </div>
  );
};

export default Help;
