import React, { CSSProperties, memo, useEffect, useRef, useState } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { ReactComponent as HelpIcon } from "assets/icons/help-mui.svg";

import { GetCoinImg, MemoTable, Menu } from "components";
import { useFetch, useLocalStorage } from "hooks";
import { getIntlValue } from "providers";

import { WithdrawRecords, WithdrawRecord } from "../types";
import css from "./style.module.scss";
import Help from "./Help";

interface Props {
  page: number;
  setTotalPages: React.Dispatch<React.SetStateAction<number>>;
  setPage: React.Dispatch<React.SetStateAction<number>>;
  setIsLoading: React.Dispatch<React.SetStateAction<boolean>>;
}

interface CoinProps {
  coin: string;
}

const Coin = memo(
  ({ coin }: CoinProps) => <GetCoinImg coin={coin} className={css.coinIcon} />,
  (p, n) => p.coin === n.coin,
);

const emptyTableStyle: CSSProperties = {
  width: "100%",
  textAlign: "center",
  fontSize: "1.5rem",
  marginTop: 30,
};

interface HelperTypes {
  anchor: HTMLDivElement | null;
  transaction_id: string;
  method: string;
  link: string;
}

const defaultHelper: HelperTypes = {
  anchor: null,
  link: "",
  method: "",
  transaction_id: "",
};

export const WithdrawTable = ({
  page,
  setTotalPages,
  setPage,
  setIsLoading,
}: Props) => {
  const intl = useGetLocale();
  const [token] = useLocalStorage("token");
  const [data, setData] = useState<WithdrawRecord[]>([]);

  const [helperData, setHelperData] = useState<HelperTypes>(defaultHelper);

  const anchorRef = useRef<HTMLDivElement>(null);

  const [logFetch, { data: logData, loading: logLoading }] =
    useFetch<WithdrawRecords>("withdraw/withdraw-log");

  const changePageFetch = (page: number) => {
    logFetch({
      variables: { token, page },
    });
  };

  useEffect(() => {
    if (logData) {
      const { current_page, total_pages, withdrawals } = logData;

      setData(withdrawals);
      setTotalPages(total_pages);
      setPage(current_page);
    }
  }, [logData]);

  useEffect(() => {
    changePageFetch(page);
  }, [page]);

  useEffect(() => {
    setIsLoading(logLoading);
  }, [logLoading]);

  const formatMessage = getIntlValue(intl, "wallet.records.");

  return (
    <div className={css.root} ref={anchorRef}>
      <MemoTable
        root={{
          className: css.table,
        }}
        head={{
          values: [
            formatMessage("details"),
            formatMessage("send_amount"),
            formatMessage("status"),
          ],
          classes: {
            thead: css.tHead,
            th: clsx(css.tCell, css.headCell),
          },
        }}
        body={{
          rows: data.map(
            (
              {
                coin,
                amount,
                send_amount,
                status,
                status_int,
                method,
                transaction_id,
                explorer,
              },
              i,
            ) => {
              let labelType = css.bDefault,
                element: HTMLDivElement | null;

              switch (Number(status_int)) {
                case 1:
                  labelType = css.bSuccess;
                  break;

                case 2:
                  labelType = css.bWarning;
                  break;

                case 3:
                  labelType = css.bPrimary;
                  break;

                case 4:
                  labelType = css.bDanger;
                  break;

                default:
                  labelType = css.bDefault;
                  break;
              }

              const id = `${transaction_id}_${send_amount}_${i}`;
              const showMenu = () => {
                setHelperData({
                  anchor: element,
                  link: explorer,
                  method,
                  transaction_id,
                });
              };

              return {
                values: [
                  <div
                    ref={(node) => (element = node)}
                    onPointerMove={showMenu}
                    onClick={showMenu}
                    className={css.helpWrapper}>
                    <HelpIcon className={css.icon} />
                  </div>,

                  <span className={css.headFlex}>
                    <Coin coin={coin} />
                    {send_amount}
                  </span>,

                  <label className={`${css.bLabel} ${labelType}`}>
                    {status}
                  </label>,
                ],
                key: id,
              };
            },
          ),
          classes: {
            tr: css.tRow,
            td: css.tCell,
          },
        }}
      />

      <Menu
        className={css.popper}
        isOpen={!!helperData.anchor}
        anchor={helperData.anchor}
        onClose={() => setHelperData(defaultHelper)}
        onMove={() => setHelperData(defaultHelper)}>
        <Help
          transaction_id={helperData.transaction_id}
          method={helperData.method}
          //@ts-ignore
          classes={css}
          link={helperData.link}
        />
      </Menu>

      {data.length === 0 && (
        <div style={emptyTableStyle}>
          {intl.formatMessage({ id: "wallet.emptyTable" })}
        </div>
      )}
    </div>
  );
};
