import React, { CSSProperties, memo, useEffect, useState } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { ReactComponent as HelpIcon } from "assets/icons/help-mui.svg";

import { getIntlValue } from "providers";
import { GetCoinImg, MemoTable, Menu } from "components";
import { useFetch, useLocalStorage } from "hooks";

import { DepositRecords, DepositRecord } from "../types";
import css from "./style.module.scss";
import Help from "./Help";

interface CoinProps {
  coin: string;
}

const Coin = memo(
  ({ coin }: CoinProps) => <GetCoinImg coin={coin} className={css.coinIcon} />,
  (p, n) => p.coin === n.coin,
);

interface Props {
  page: number;
  setTotalPages: React.Dispatch<React.SetStateAction<number>>;
  setPage: React.Dispatch<React.SetStateAction<number>>;
  setIsLoading: React.Dispatch<React.SetStateAction<boolean>>;
}

const emptyTableStyle: CSSProperties = {
  width: "100%",
  textAlign: "center",
  fontSize: "1.5rem",
  marginTop: 30,
};

interface HelpType {
  transaction_id: string;
  anchor: HTMLDivElement | null;
  link: string;
  date: string;
  credited: string;
  confirmations: string;
  coin: string;
}

const defaultHelp: HelpType = {
  anchor: null,
  coin: "",
  confirmations: "",
  credited: "",
  date: "",
  link: "",
  transaction_id: "",
};

export const DepositTable = ({
  page,
  setTotalPages,
  setPage,
  setIsLoading,
}: Props) => {
  const intl = useGetLocale();
  const [token] = useLocalStorage("token");
  const [data, setData] = useState<DepositRecord[]>([]);
  const [showHelp, setShowHelp] = useState(false);
  const [helperMenuData, setHelperMenuData] = useState<HelpType>(defaultHelp);

  const [logFetch, { data: logData, loading: logLoading }] =
    useFetch<DepositRecords>("/deposit/deposit-log");

  const changePageFetch = (page: number) => {
    logFetch({
      variables: { token, page },
    });
  };

  useEffect(() => {
    if (logData) {
      const { current_page, total_pages, deposits } = logData;

      setData(deposits);
      setTotalPages(total_pages);
      setPage(current_page);
    }
  }, [logData]);

  useEffect(() => {
    changePageFetch(page);
  }, [page]);

  useEffect(() => {
    setIsLoading(logLoading);
  }, [logLoading]);

  const formatMessage = getIntlValue(intl, "wallet.records.");

  return (
    <div className={css.root}>
      <MemoTable
        root={{
          className: css.root,
        }}
        head={{
          values: [
            formatMessage("details"),
            formatMessage("amount"),
            formatMessage("method"),
          ],

          classes: {
            thead: css.tHead,
            th: clsx(css.tCell, css.headCell),
          },
        }}
        body={{
          rows: data.map(
            (
              {
                coin,
                amount,
                date,
                credited,
                method,
                confirmations,
                transaction_id,
                explorer,
              },
              i,
            ) => {
              let labelType = css.bDefault;

              const id = `${transaction_id}_${date}_${i}`;
              let element: HTMLDivElement | null = null;

              const openHelp = () => {
                setHelperMenuData({
                  anchor: element,
                  coin,
                  confirmations,
                  credited,
                  date,
                  link: explorer,
                  transaction_id,
                });
              };
              return {
                values: [
                  <div
                    ref={(node) => (element = node)}
                    onPointerMove={openHelp}
                    onClick={openHelp}
                    className={css.helpWrapper}>
                    <HelpIcon className={css.icon} />
                  </div>,

                  <span className={css.headFlex}>
                    <Coin coin={coin} />
                    {amount}
                  </span>,

                  <label className={`${css.bLabel} ${labelType}`}>
                    {method}
                  </label>,
                ],
                key: id,
              };
            },
          ),

          classes: {
            tbody: css.tabBody,
            tr: css.tRow,
            td: css.tCell,
          },
        }}
      />

      <Menu
        onClose={() => setHelperMenuData(defaultHelp)}
        onMove={() => setHelperMenuData(defaultHelp)}
        className={css.popper}
        anchor={helperMenuData.anchor}
        isOpen={!!helperMenuData.anchor}>
        <>
          <Help
            show={showHelp}
            transaction_id={helperMenuData.transaction_id}
            link={helperMenuData.link}
            date={helperMenuData.date}
            credited={helperMenuData.credited}
            confirmations={helperMenuData.confirmations}
            coin={helperMenuData.coin}
          />
        </>
      </Menu>

      {data.length === 0 && (
        <div style={emptyTableStyle}>
          {intl.formatMessage({ id: "wallet.emptyTable" })}
        </div>
      )}
    </div>
  );
};
