import React, { FC, useMemo, useRef } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import css from "./style.module.scss";

interface Props {
  show: boolean;
  transaction_id: string;
  link: string;

  date: string;
  confirmations: string;
  credited: string;
  coin: string;
}

const Help: FC<Props> = ({
  show,
  transaction_id,
  link,
  date,
  confirmations,
  credited,
}) => {
  const intl = useGetLocale();

  const rootRef = useRef<HTMLDivElement>(null);

  const labelCss = useMemo(() => {
    switch (credited) {
      case "Credited":
        return css.bSuccess;

      case "Awaiting Confirmations":
        return css.bPrimary;

      default:
        return css.bDefault;
    }
  }, [credited]);

  return (
    <div className={css.root} ref={rootRef}>
      <div className={css.wrapper}>
        <h5 className={css.title}>
          {intl.formatMessage({ id: "wallet.records.transaction_id" })}:
        </h5>
        {transaction_id ? (
          <a
            href={link === "#" ? "" : link}
            rel="noreferrer"
            target="_blank"
            className={clsx(css.url, css.textId)}>
            {transaction_id}
          </a>
        ) : (
          <span>-</span>
        )}
      </div>
      <div className={css.wrapper}>
        <h5 className={css.title}>
          {intl.formatMessage({ id: "wallet.records.date" })}:
        </h5>
        <p className={css.text}>{date}</p>
      </div>
      <div className={css.wrapper}>
        <h5 className={css.title}>
          {intl.formatMessage({ id: "wallet.records.confirmations" })}:
        </h5>
        <p className={css.text}>{confirmations}</p>
      </div>
      <div className={css.wrapper}>
        <h5 className={css.title}>
          {intl.formatMessage({ id: "wallet.records.credited" })}:
        </h5>
        <label className={`${css.bLabel} ${labelCss}`}>{credited}</label>
      </div>
    </div>
  );
};

export default Help;
